<template>
  <v-app>
    <v-container align="center">
      <div class="row">
        <v-btn
          type="button"
          class="btn btn-success btn-sm"
          v-b-modal.job-modal
          @click="addJob"
        >Add Job</v-btn>
        <v-btn
          type="button"
          class="btn btn-danger btn-sm"
          v-b-modal.month-modal
          @click="getTimeSheetsApproved"
        >View All Unpaid</v-btn>
        <v-container>
          <div class="parent">
            <div class="col">
              <v-combobox v-model="search.lead" label="Employee" :items="employees" />
            </div>
            <div class="col">
              <v-combobox v-model="search.Client" label="Client" :items="clients" />
            </div>
            <div class="col">
              <v-combobox v-model="search.Job" multiple label="Job" :items="jobs" />
            </div>
            <div class="col">
              <v-menu
                ref="menu"
                v-model="menu1"
                :close-on-content-click="false"
                transition="scale-transition"
                offset-y
                full-width
                min-width="290px"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field
                    v-model="search.Start"
                    label="Start"
                    prepend-icon="event"
                    readonly
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  first-day-of-week="1"
                  ref="picker"
                  dark
                  v-model="search.Start"
                  @change="change"
                ></v-date-picker>
              </v-menu>
            </div>
            <div class="col">
              <v-menu
                ref="menu"
                v-model="menu2"
                :close-on-content-click="false"
                transition="scale-transition"
                offset-y
                full-width
                min-width="290px"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field
                    v-model="search.End"
                    label="End"
                    prepend-icon="event"
                    readonly
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  first-day-of-week="1"
                  ref="picker"
                  dark
                  v-model="search.End"
                  @change="change"
                ></v-date-picker>
              </v-menu>
            </div>
          </div>
        </v-container>
        <table class="table table-hover">
          <thead>
            <tr>
              <th scope="col" >Employee</th>
              <th scope="col" >Date</th>
              <th scope="col" >Client</th>
              <th scope="col" >Job Name</th>
              <th scope="col" >Hours</th>
              <th scope="col" >Paid</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(hour, index) in hours" :key="index">
              <td >{{ hour.Employee_Name }}</td>
              <td >{{ hour.Date }}</td>
              <td >{{ hour.Client }}</td>
              <td >{{ hour.Job_Name }}</td>
              <td >{{ hour.Hours}}</td>
              <td >{{ hour.Paid}}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </v-container>
    <v-snackbar :bottom="true" :left="true" v-model="show" color="success" dark>
      <v-icon color="white" class="mr-3">mdi-bell-plus</v-icon>

      <div>Submitted</div>

      <v-icon size="16" @click="show = false">mdi-close-circle</v-icon>
    </v-snackbar>
  </v-app>
</template>

<script>
import Axios from "axios";
import JobForm from "@/components/JobForm.vue";
import Activity from "@/components/Activity.vue";
export default {
  components: {
    JobForm,
    Activity
  },

  computed: {
    hours() {
      let list = this.$store.getters.getAllHours;
      if (this.search.Employee_Name != "" && this.search.Employee_Name != null) {
        list = list.filter(
          item => item.Employee_Id == this.search.Employee_Name.value
        );
      }
      if (this.search.Job != "" && this.search.Job != null) {
        var tempList = []
        for (var i = 0; i < this.search.status.length; i++) {
          var temp = [...list]
          temp = list.filter(
            item => item.Job == this.search.Job
          );
          tempList = tempList.concat(temp);
        }
        list = tempList
      }
      

      if (this.search.Start !== "" && this.search.End !== "") {
        list = list.filter(
          item => new Date(item.Date) > new Date(this.search.Start)
        );
        list = list.filter(
          item => new Date(item.Date) < new Date(this.search.End)
        );
      }

      if (this.search.Client !== null) {
      	list = list.filter(item => item.Client.includes(this.search.Client));
    }  
	return list;
    },
    employees() {
      return this.$store.getters.getEmployeeList;
    },
    clients () {
      let jobs = this.$store.getters.getJobs;
      var clients = []
      for(var i = 0; i < jobs.length; i++) {
        clients.push(jobs[i].Job.Client)
      }
      return clients

    },
    jobs () {
      let jobsList = this.$store.getters.getJobs;
      var jobs = []
      for(var i = 0; i < jobsList.length; i++) {
        jobs.push(jobsList[i].Job.Name)
      }
      return jobs

    }
	
  },

  data() {
    return {
      show: false,
      active: null,
      search: {
        Employee_Name: "",
        Client: "",
        Job: "",
        Start: "",
        End: ""
      },
    };
  },
  methods: {
    change() {
      this.menu1 = false;
      this.menu2 = false;
    }
  }
};
</script>




